<template>
        <!-- :class="{'-top-72 duration-700 h-0 opacity-0': !produkVirtual}" -->
    <div 
        class="bl-popover hidden lg:block z-30"
        :class="{ 'is-active': categoryProduct }"
    >
        <div class="bl-container h-full" style="max-width: 1063px">
            <div class="flex-item justify-between pb-6 border-b">
                <h1 class="text-3xl font-semibold">Kategori Barang</h1>
                <svg @click="toggleCategoryProduct()" data-v-9789980a="" version="1.1" viewBox="0 0 24 24" class="arrow svg-icon svg-fill cursor-pointer" style="width: 24px; height: 24px;"><path fill="#5C6066" stroke="none" pid="0" fill-rule="evenodd" clip-rule="evenodd" d="M18.707 17.293a.999.999 0 11-1.414 1.414L12 13.414l-5.293 5.293a.997.997 0 01-1.414 0 .999.999 0 010-1.414L10.586 12 5.293 6.707a.999.999 0 111.414-1.414L12 10.586l5.293-5.293a.999.999 0 111.414 1.414L13.414 12l5.293 5.293z" _fill="#000"></path></svg>
            </div>
            <div class="flex h-full">
                <section class="w-4/12 pb-36">
                    <ul class="popover-menu overflow-auto h-full"
                        style="max-height: 432px"
                    >
                        <template v-for="(menu, i) in menuList" :key="i">
                            <li @mouseover="currentMenu = menu">
                                <a href="#"
                                   :class="{ 'is-active': currentMenu == menu }"
                                   class="trasnform duration-700"
                                >
                                    {{ menu }}
                                    <svg version="1.1" viewBox="0 0 20 20" class="svg-icon svg-fill" style="width: 20px; height: 20px;"><path fill="#5C6066" stroke="none" pid="0" fill-rule="evenodd" clip-rule="evenodd" d="M13.707 10.707l-5 5a.997.997 0 01-1.414 0 .999.999 0 010-1.414L11.586 10 7.293 5.707a.999.999 0 111.414-1.414l5 5a1 1 0 010 1.414z" _fill="#000"></path></svg>
                                </a>
                            </li>
                        </template>
                        <a href="#" class="block mx-4 my-5 text-blue-500 text-lg">Lainnya</a>
                    </ul>
                    <div class="mb-4 p-4">
                        <button class="py-2 border-gray-700 border-2 rounded font-semibold text-lg w-full">
                            Lihat semua kategori
                        </button>
                    </div>
                </section>
                <section class="w-10/12 p-6 pr-0">
                    <div class="grid grid-cols-3 gap-6 h-full overflow-y-auto">
                        <PopoverSubmenu :menu="currentMenu" />
                    </div>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
import { computed, ref } from 'vue'
import { useStore } from 'vuex'
import PopoverSubmenu from '@/components/PopoverSubmenu'

export default {
    components: {
        PopoverSubmenu
    },
    setup() {
        const store = useStore()

        const currentMenu = ref('Sepeda')
        const menuList = ref([
            'Sepeda', 'Motor', 'Mobil', 'Elektronik', 'Handphone', 'Komputer', 'Tablet', 'Laptop'
        ])

        const categoryProduct = computed(() => store.state.categoryProduct)
        const toggleCategoryProduct = () => store.commit('toggleCategoryProduct')

        function toggleMenuActive(e, active) {
            const el = e.target
            if (active)
                el.classList.add('is-active')
            else
                el.firstChild.classList.remove('is-active')
        }

        return {
            toggleCategoryProduct, categoryProduct, toggleMenuActive, currentMenu, menuList
        }
    }
}
</script>

<style>
.popover-menu > li > a {
    padding: 12px 16px;
    font-size: 16px;
}
.popover-menu > li > a:hover {
    background-color: #f2f2f2;
}
.popover-menu > li:first-child {
    margin-top: 12px;
}
.popover-menu > li > a.is-active {
    background-color: #f2f2f2;
    font-weight: 600;
}
.popover-menu > li > a {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
